import { validateForms } from "../functions/validate-forms.js"

const contactsRules = [
  {
    ruleSelector: "[data-input='name']",
    rules: [
      {
        rule: 'customRegexp',
        value: /^[a-zA-Zа-яА-Я'][a-zA-Zа-яА-Я-' ]+[a-zA-Zа-яА-Я']?$/u,
        errorMessage: 'Недопустимый формат',
      },
      {
        rule: 'minLength',
        value: 1,
      },
      {
        rule: 'required',
        value: true,
        errorMessage: 'Обязательное поле',
      }
    ]
  },
  {
    ruleSelector: "[data-input='email']",
    rules: [
      {
        rule: 'required',
        value: true,
        errorMessage: 'Обязательное поле',
      },
      {
        rule: 'email',
        errorMessage: 'Недопустимый формат',
      },
    ]
  }
];

const aboutRules = [
  {
    ruleSelector: "[data-input='email']",
    rules: [
      {
        rule: 'required',
        value: true,
        errorMessage: 'Обязательное поле',
      },
      {
        rule: 'email',
        errorMessage: 'Недопустимый формат',
      }
    ]
  }
]

validateForms('.application__form', contactsRules);
validateForms('.subscribe__form', aboutRules);

