export default (function () {
  const search = document.querySelector('.search');
  const searchForm = document.querySelector('.search__form');

  search.addEventListener('click', (e) => {
    if (e.target.classList.contains("search__btn") || e.target.closest('.search__btn')) {
      search.querySelector('.search__input').value = '';
      search.querySelector('.search__bar').classList.add('search__bar--active');
      e._isSearchBarOpen = true;
    } else if (e.target.classList.contains("search__close") || e.target.closest('.search__close')) {
      search.querySelector('.search__bar').classList.remove('search__bar--active');
    }

    e._isClickWithinSearchBar = true;
  })

  searchForm.addEventListener('submit', (e) => {
    e.preventDefault();
  })

  window.addEventListener('click', (e) => {
    if (e._isClickWithinSearchBar) return;
    search.querySelector('.search__bar').classList.remove('search__bar--active');
  })
})();
