export default (function contactsShow() {
  const contactsField = document.querySelector(".location");
  const contactsFieldLocation = document.querySelector(".location__street");

  contactsField.addEventListener('click', (e) => {
    if (e.target.classList.contains("location__close") || e.target.closest('.location__close')) {
      contactsFieldLocation.classList.add('overflow-hide');
    }

    if (e.target.classList.contains("location__open") || e.target.closest('.location__open')) {
      contactsFieldLocation.classList.remove('overflow-hide');
    }
  })
})();
