let center = [55.764679419786255,37.62812178328897];

function init() {
  loaderWrapper.remove();

  const map = new ymaps.Map('location-map', {
    center: center,
    zoom: 14,
  });

  map.controls.remove("geolocationControl"); // удаляем геолокацию
  map.controls.remove("searchControl"); // удаляем поиск
  map.controls.remove("trafficControl"); // удаляем контроль трафика
  map.controls.remove("typeSelector"); // удаляем тип
  map.controls.remove("fullscreenControl"); // удаляем кнопку перехода в полноэкранный режим
  map.controls.remove("zoomControl"); // удаляем контрол зуммирования
  map.controls.remove("rulerControl"); // удаляем контрол правил
  map.behaviors.disable(["scrollZoom"]); // отключаем скролл карты (опционально)

  var placemark = new ymaps.Placemark([55.77100502279006, 37.63474331712723], {},
    {
      iconLayout: 'default#image',
      iconImageHref: '../img/marker.svg',
      iconImageSize: [12, 12],
    }
  );




  map.geoObjects.add(placemark);
}

const locationMap = document.getElementById("location-map");
const loaderWrapper = document.createElement("div");
const loader = document.createElement("div");
loaderWrapper.style.cssText =
  "width: 100%; height: 100%; display: flex; justify-content: center; align-items: center";
loader.classList.add("loader");
loaderWrapper.append(loader);
loaderWrapper.append;
locationMap.append(loaderWrapper);

ymaps.ready(init);
